import { Title, Meta } from '@angular/platform-browser';
import { ArticlesList } from './../articles/articles.constant';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-article-details',
  templateUrl: './article-details.component.html',
  styleUrls: ['./article-details.component.scss']
})
export class ArticleDetailsComponent implements OnInit {
  articles = [];
  article: any;

  constructor(private router: ActivatedRoute, private titleService: Title, private metaService: Meta) { }

  ngOnInit() {
    const title = this.router.snapshot.params.title;
    this.articles = ArticlesList;
    this.article = this.articles.filter(article => article.title.replace(/ /g, '-').toLowerCase() === title)[0];
    this.titleService.setTitle(this.article.title);
    this.metaService.updateTag({ name: 'description', content: this.article.content });
  }
}
